const initialState = imutable(InitialState)

export const Init = (state = initialState, action) => {
    switch (action.type) {
        case 'ACTION_TYPE':
            return
        default:
            return state
    }
}

export function getNavigation(state) {
    return state.Init.navigation
}

export function getRewiews(state) {
    return state.Init.rewiews
}

export function getWorkExamples(state) {
    return state.Init.workExamples
}
export function getAdvantages(state) {
    return state.Init.advantages
}

export function getServiceInfo(state) {
    return state
        .Init
        .navigation
        .filter((val) => val.isDropdown)[0]
        .DropdownItems
}

export function getService(name, state) {
    return state
        .Init
        .service
        .find(sv => sv.name === name)
}

export function getFooterInstagram(state, type = 'footer') {

    return state
        .Init
        .instagram
        .find((v) => v.name === type)
}


